/** Homepage Section */
.homepage {
    width: 100%;
    height: calc(100% - 55px);
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    justify-items: center;
    gap: 40px;
    overflow-y: auto;
}

/* Homepage Leftbar Section */
.homepageLeftbar {
    position: sticky;
    top: 0px;
    width: 100%;
    height: 92vh;
    padding: 16px 0 0 8px;
    overflow-y: auto;
    overflow-x: hidden;
}

.homepageLeftbar a {
    text-decoration: none;
    color: initial;
}

.homepageLeftbar .terms {
    margin: 6px 0 0 12px;
}

.homepageLeftbar .terms p {
    font-size: 12px;
    font-weight: 400;
    white-space: pre-wrap;
    color: gray;
}

.homepageLeftbar .terms p span:hover {
    cursor: pointer;
    text-decoration: underline;
}

/* HomePage Feed Section */
.homepageFeed {
    max-width: 600px;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 16px 0;
}

/* HomePage Rightbar Section */
.homepageRightbar {
    position: sticky;
    top: 0px;
    width: 100%;
    height: 92vh;
    padding: 16px 8px 0 0;
    overflow-y: auto;
    overflow-x: hidden;
}

.homepageRightbarTop {
    border-bottom: 1px solid lightgray;
}

/* HomePage NewMessage Section */
.newMsg {
    position: absolute;
    bottom: 15px;
    right: 30px;
    display: flex;
    align-items: center;
    padding: 18px;
    border-radius: 50%;
    box-shadow: 1px 1px 10px 1px rgb(0, 0, 0, 0.1);
    background-color: #fff;
}

.newMsg:hover {
    cursor: pointer;
    background-color: #F2F5F7;
}

.newMsg #newMsgIcon {
    width: 20px;
    height: 20px;
    display: inline-block;
    background-image: url("https://static.xx.fbcdn.net/rsrc.php/v3/yG/r/xFYw0bOHCD1.png");
    background-position: 0px -105px;
    background-size: 21px 304px;
}

/** Responsive Media Queries */
@media only screen and (max-width: 950px) {

    /** Homepage Section */
    .homepage {
        grid-template-columns: 2fr 1fr;
    }

    /* Homepage Leftbar Section */
    .homepageLeftbar {
        display: none;
    }

    /* HomePage Feed Section */
    .homepageFeed {
        padding: 16px 0px 16px 16px;
    }
}

@media only screen and (max-width: 680px) {

    /** Homepage Section */
    .homepage {
        grid-template-columns: 1fr;
        gap: 0px
    }

    /* Homepage Leftbar Section */
    .homepageLeftbar {
        display: none;
    }

    /* HomePage Feed Section */
    .homepageFeed {
        max-width: 100%;
        padding: 16px;
    }

    /* HomePage Rightbar Section */
    .homepageRightbar {
        display: none;
    }
}